const monthNames = [
  'January',
  'February',
  'March',
  'April',
  'May',
  'June',
  'July',
  'August',
  'September',
  'October',
  'November',
  'December',
];

const DateParser = createdAt => {
  const date = new Date(createdAt);
  const day = date.getDate(); //     1  31
  const month = monthNames[date.getMonth()];
  const year = date.getUTCFullYear(); //   (4 )
  const hours = date.getUTCHours(); //    UTC ( 0  23)
  const minutes = date.getUTCMinutes().toString().padStart(2, '0'); //    UTC ( 0  59),     
  return { day, month, year, hours, minutes };
};

export default DateParser;
