import { AiOutlinePlusSquare } from 'react-icons/ai';
import { Input, Ul, Li, Button, SearchBar } from './Controls.styled';
import cssVar from 'utilities/cssVarGetter';
import Select from 'react-select';
import { Link, useLocation, useSearchParams } from 'react-router-dom';
import { useEffect, useRef, useState } from 'react';

const options = [
  { value: 'asc', label: 'New first' },
  { value: 'desc', label: 'Old first' },
];

export const Controls = ({ isDesktop }) => {
  const [isProfilePage, setIsProfilePage] = useState(false); //   Go Back
  const location = useLocation(); //   Go Back
  const backLinkLocationRef = useRef(location.state?.from ?? '/'); //   Go Back
  const [searchParams, setSearchParams] = useSearchParams(); //    URLSearchParams  
  const [authorSearch, setAuthorSearch] = useState(''); //   Input Author
  const [querySearch, setQuerySearch] = useState(''); //   Input Query
  const [sort, setSort] = useState(options[0]);

  //  URLSearchParams  SearchBar
  const updateSearchParams = (newSort = sort) => {
    const params = {};

    if (authorSearch.length) params.author = authorSearch;
    if (querySearch.length) params.q = querySearch;
    if (newSort) {
      params._sort = 'createdAt'; //    'createdAt'
      params._order = newSort.value; //   - asc  desc
    }
    setSearchParams(params);
  };

  //    URLSearchParams     Input value
  const handleBlur = () => {
    updateSearchParams();
  };

  //  Select   
  const handleSelect = e => {
    setSort(e);
    updateSearchParams(e);
  };

  // Input value  Select   URLSearchParams
  useEffect(() => {
    setAuthorSearch(searchParams.get('author') ?? '');
    setQuerySearch(searchParams.get('q') ?? '');

    const _sort = searchParams.get('_sort');
    const _order = searchParams.get('_order');

    let sortValue = null;

    //  _sort  'createdAt'  _order  'asc', ,   
    if (_sort === 'createdAt' && _order === 'asc') {
      sortValue = options.find(opt => opt.value === 'asc');
    }
    //  _sort  'createdAt'  _order  'desc', ,   
    if (_sort === 'createdAt' && _order === 'desc') {
      sortValue = options.find(opt => opt.value === 'desc');
    }

    setSort(sortValue);
  }, [searchParams]);

  //  /profile    Input Author,    Go Back
  useEffect(() => {
    setIsProfilePage(location.pathname.startsWith('/profile'));
  }, [location]);

  return (
    <>
      <SearchBar onBlur={handleBlur}>
        <Ul>
          {isProfilePage ? (
            <Li>
              <Link to={backLinkLocationRef.current}> Go Back</Link>
            </Li>
          ) : (
            <Li>
              <label htmlFor="author">Username</label>
              <Input
                autoComplete="off"
                type="text"
                placeholder="Enter username to search"
                name="author"
                value={authorSearch}
                onChange={e => {
                  setAuthorSearch(e.target.value);
                }}
              />
            </Li>
          )}
          <Li>
            <label htmlFor="q">Message</label>
            <Input
              autoComplete="off"
              type="text"
              placeholder="Enter to search"
              name="q"
              value={querySearch}
              onChange={e => {
                setQuerySearch(e.target.value);
              }}
            />
          </Li>
          <Li>
            <label htmlFor="sort">Sort</label>
            <Select
              name="sort"
              id="sort"
              options={options}
              isSearchable={false}
              onChange={handleSelect}
              value={sort}
            />
          </Li>
          <Li>
            {!isDesktop && <label htmlFor="sort">Add new post:</label>}
            <Button>
              <AiOutlinePlusSquare
                size={isDesktop ? 50 : 36}
                color={cssVar('--primary-light')}
              />
            </Button>
          </Li>
        </Ul>
      </SearchBar>
    </>
  );
};
